@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body{font-family:"IBM Plex Mono", monospace;padding:0;margin:0;background:linear-gradient(to right, #ebeff1, #bac7c2, #d6d0da, #bac7c2, #c7abda, #bac7c2, #ebeff1)}.logo h1{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;color:white;width:2rem;height:2rem;border-radius:50%;background-image:linear-gradient(to left, #9b769b, #c7abda, #c4e2d1);position:absolute;left:1rem;font-family:"Dancing Script", cursive}.img-slider img{width:30rem;height:30rem;border-radius:10%}.img-slider{display:block;padding-left:20%;padding-right:40%}.GalaryFoto{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;padding:1rem;margin:2rem}li:nth-child(2n){-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.GalaryFoto .picture{padding:1rem;width:18rem;height:15rem;border-radius:10%}.GalaryFoto .picture:hover{width:18.5rem;height:15.5rem;box-shadow:10px 5px 5px #646363}.main-page{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;justify-items:center;padding:1rem}.link-button{background-image:linear-gradient(to left, #9b769b, #c7abda, #c4e2d1);width:10rem;height:2rem}.link-button:hover{background-image:linear-gradient(to left, #7a557a, #663f80, #3e7254);color:white;border:2px solid #00cec9}.menu,.contact{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:row;flex-direction:row;justify-items:center;width:100vw;height:7vh;background-color:azure}.footer{display:-webkit-flex;display:flex;position:absolute;bottom:0rem;background-color:whitesmoke;width:100vw;height:7vh}li{padding:1rem;list-style:none}a{text-decoration:none}.about,.home,.skills{height:83vh}.about{width:60%;padding:4rem}

